import { Component, OnInit, Input } from '@angular/core';
import { SlideInOutAnimations } from '../animation';

@Component({
  selector: 'app-collapse',
  templateUrl: './collapse.component.html',
  styleUrls: ['./collapse.component.css'],
  animations: [SlideInOutAnimations]
})
export class CollapseComponent implements OnInit {
  @Input() option: any;
  constructor() { }

  ngOnInit() {
    if (this.option) {
      this.option.array.forEach((element: any) => {
        if (element.isOpen) {
          element.animations = 'in';
        } else {
          element.animations = 'out';
        }
        if (element.group) {
          element.group.forEach((x: any) => {
            if (x.isOpen) {
              x.animations = 'in';
            } else {
              x.animations = 'out';
            }
          });
        }
      });
    }
  }

  click(index: any, item: any) {
    this.option.array.forEach((parentElem: any, indexParent: any) => {
      if (index.indexOf('parent') !== -1) {
        if ((index === 'parent' + indexParent)) {
          item.animations = item.animations === 'out' ? 'in' : 'out';
        } else {
          parentElem.animations = 'out';
        }
      }
      if (parentElem.group) {
        parentElem.group.forEach((subElem: any, indeSub: any) => {
          if ((index === 'child' + indeSub)) {
            item.animations = item.animations === 'out' ? 'in' : 'out';
          } else {
            subElem.animations = 'out';
          }
        });
      }
    });
  }

}
